import { Component, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { IsubscriptionPackage } from '@biz-nest/core-client';
import {
  AppNameService, CurrencyService, EventbusService,
  MainBarTitleService,
  MakeSubscriptionCoreComponent,
  scaleSmooth
} from '@front-shared-lib';
import { navByBus, subscriptionPackages } from '@main-shared-lib';

@Component({
  selector: 'app-subscription-packages',
  standalone: true,
  imports: [
    MakeSubscriptionCoreComponent
  ],
  animations: [scaleSmooth],
  templateUrl: './subscription-packages.component.html',
  styleUrl: './subscription-packages.component.scss'
})
export class SubscriptionPackagesComponent
implements OnInit {
  id: string;
  subscriptionPackanges = subscriptionPackages as Required<IsubscriptionPackage>[];
  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  constructor(
    private eventbus: EventbusService,
    private route: ActivatedRoute,
    private router: Router,
    public currencyService: CurrencyService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) {
    this.id = this.route.snapshot?.params['id'];
  }

  ngOnInit(): void {
    this.mainBarTitle.title = 'Subscriptions Packages - Manage Subscription Packages';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: 'Easily manage and view your subscription packages for tailored service options.'
    });
  }

  editSubscription(sub?) {
    this.navigateByBus(['/subscription/update'], 'updateSubscription', sub);
  }
}
